    import { mapState } from 'vuex'
	import { getHomeList } from '../../api/mainApi.js'
    export default {
		data() {
			return {
				homeList:{},
				banner:[], // 轮播图
				navs:[],   // 导航nav
				advs:[],   // 广告
				seles:[],  // 精选
				indicatorDots: false,
				autoplay: true,
				interval: 3000,
				duration: 500,
			}
		},
        computed: mapState(['forcedLogin', 'hasLogin', 'userName']),
        onLoad() {
			// 获取首页banner
			this.getHomeList()
        },
		methods:{
			// 获取首页banner
			getHomeList(){
				getHomeList({ parentId: 1 }).then((res) => {
					if(res.code == '1000'){
						this.homeList = res.data
					}
				})
			},
			goNextPage(url){
				if(url){
					uni.navigateTo({
					    url
					});
				}
			}
		}
    }
